* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background: #e5f0f8;
  color: #222;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}
.booklist {
  width: 100vw;
  max-width: 880px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
  /* border: 2px solid black; */
}

@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
  }
}
.book {
  background: #fff;
  border-radius: 1rem;
  padding: 2rem 5rem;
  text-align: center;
  position: relative;
  /* border: 2px solid red; */
}
.book h2 {
  margin-top: 1rem;
  font-size: 1.25rem;
}
.book img {
  width: 100%;
  object-fit: cover;
}
.book h4 {
  color: #617d98;
  font-size: 0.75rem;

  margin-top: 0.5rem;
}

.number {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  font-size: 1rem;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  background-color: #c35600;
  color: #fff;
}

h1 {
  text-align: center;
  margin-top: 4rem;
  text-transform: capitalize;
}
